import { collection, doc, getDoc, getDocs, addDoc, updateDoc, deleteDoc, } from "firebase/firestore";
import { db } from "../../firebase"


// 액션 타입을 정해줍니다.
const CREATE = "word/CREATE";
const LOAD = "word/LOAD"



// 초기 상태값을 만들어줍니다.
const initialState = {

    list: []
};


export const createWord = (word) => {
    return { type: CREATE, word };
};


export const loadWord = (word_list) => {
    return { type: LOAD, word_list }
}


export const loadWordFB = () => {
    return async function (dispatch) {
        const word_data = await getDocs(collection(db, "word"));

        let word_list = [];

        word_data.forEach((w) => {
            word_list.push({ id: w.id, ...w.data() })
        });

        dispatch(loadWord(word_list))
    }
}

export const addWordFB = (word) => {
    return async function (dispatch) {

        const docRef = await addDoc(collection(db, "word"), word);
        const word_data = { id: docRef.id, ...word };

        dispatch(createWord(word_data))

    }
}


// 리듀서예요.
// 실질적으로 store에 들어가 있는 데이터를 변경하는 곳이죠!
export default function reducer(state = initialState, action = {}) {
    switch (action.type) {

        case "word/LOAD": {
            return { list: action.word_list };
        }

        case "word/CREATE":
            const new_word_list = [...state.list, action.word];
            return { ...state, list: new_word_list };

        default:
            return state;
    }
}